import { Component, OnInit } from '@angular/core';
import { posts } from '../../../data/blog-posts';
import { brands } from '../../../data/shop-brands';
// import { products } from '../../../data/shop-products';
import { categories } from '../../../data/shop-block-categories';
import { ApiService } from '../../shared/services/api.service';
import { ProductService } from '../../shared/services/product.service';
import { Product } from '../../shared/models/product.model';
import { ApiResponse } from 'src/app/shared/models/api-response.model';

@Component({
  selector: 'app-page-home-udon',
  templateUrl: './page-home-udon.component.html',
  styleUrls: ['./page-home-udon.component.sass'],
  providers: [ ApiService, ProductService ]
})
export class PageHomeUdonComponent implements OnInit {
  error: any;
  products: Array<Product> = [];
  new_products: Array<Product> = [];
  bestSeller_products: Array<Product> = [];
  categories = categories;
  posts = posts;
  brands = brands;

  ngOnInit() {

  }

  fetchPopularProduct() {
    this.apiService.getWeeklyPoppularProduct().subscribe(
        (response: ApiResponse<Array<Product>>) => {
            this.products = [...response.data];
        },
        (err) => this.error = err
    );
    /*this.productService.getPopular().subscribe((data: Array<Product>) => {
        setTimeout(() => {
          this.products = [...data];
        }, 1000);
      }, // success path
      error => this.error = error // error path
    );*/
  }

  fetchNewProduct() {
    this.apiService.getLastestProduct(10).subscribe(
        (response: ApiResponse<Array<Product>>) => {
            this.new_products = [...response.data];
        },
        (err) => this.error = err
    );
  }

  fetchBestSellerProduct() {
    this.apiService.getWeeklyBestSellerProduct().subscribe(
        (response: ApiResponse<Array<Product>>) => {
            this.bestSeller_products = [...response.data];
        },
        (err) => this.error = err
    );
  }


  constructor(
    private productService: ProductService,
    private apiService: ApiService
  ) {
    this.fetchPopularProduct();
    this.fetchNewProduct();
    this.fetchBestSellerProduct();
  }
}
