import { Component, Input } from '@angular/core';
import { Link } from '../../interfaces/link';
import { Shop } from '../../models/shop.model';
import * as moment from 'moment';

@Component({
    selector: 'app-page-shop-header',
    templateUrl: './page-shop-header.component.html',
    styleUrls: ['./page-shop-header.component.scss']
})
export class PageShopHeaderComponent {
    @Input() header: string;
    @Input() shop: Shop;
    @Input() breadcrumbs: Link[] = [];

    getDatetime(t: string) {
        if (!t) {
            return '';
        }
        return moment(t).locale('th').format('ll');
    }

    constructor() {

     }
}
